const OtherAttendanceTable = (props) => {
  const { t } = useCustomTranslation()
  const {
    otherAttendanceTabs,
    isLoading,
    currentPage,
    setCurrentPage,
    empName,
    tabType,
    activeTabType,
    activeTab,
    departmentName,
    startDate,
    endDate,
    handleFilterDateWiseData,
    toggleAttendanceLogs,
    selfAttendanceData,
    myTeamAttendanceData
  } = props
  const { selfAttendancePending, myTeamAttendancePending } = useSelector(state => state.otherAttendance)
  /*  -----------------Code Start Self Attendance Section--------------- */
  const selfLogFields = [
    { key: 'sn', label: 'S.N' },
    { key: 'attendance_date', label: 'Date' },
    { key: 'first_check_in_time', label: 'Check In' },
    { key: 'last_check_out_time', label: 'Check Out' },
    { key: 'total_break_time', label: 'Break Time' },
    { key: 'total_working_hours', label: 'Working Hours' },
    { key: 'first_half_status', label: 'First Half Status' },
    { key: 'second_half_status', label: 'Second Half Status' },
    { key: 'attendance_status', label: 'Status' },
    { key: 'action', label: 'Action' }
  ];

  const selfLogsScopedSlots = useMemo(() => ({
    sn: (_, index) => (
      <span>{index + 1}</span>
    ),
    attendance_date: item => dateFormate(item?.attendance_date),

    first_check_in_time: item => dateFormate(item?.first_check_in_time,"LTS"),
    last_check_out_time: item => dateFormate(item?.last_check_out_time,"LTS"),

    first_half_status: (item) => (
      <span className={`font-weight-bold ${checkAttendanceStatus(item?.first_half_status)}`}
      >{item?.first_half_status}</span>
    ),
    second_half_status: (item) => (
      <span className={checkAttendanceStatus(item?.second_half_status)}
      >{item?.second_half_status}</span>
    ),
    attendance_status: (item) => (
      <span className={checkAttendanceStatus(item?.attendance_status)}
      >{item?.attendance_status}</span>
    ),
    created_at: item => dateFormate(item?.created_at),

    action: item => (
      <span onClick={() => toggleAttendanceLogs(item)}>
        <img src="images/nextaction-Icon.svg" alt="" width={27} />
      </span>
    )
  }), [selfLogsData]);

  /* --------------Code End Self Attendance Section---------------------- */

  /* --------------Code Start Team Section---------------------------- */
  const empAttendanceSummaryFields = [
    { key: 'sn', label: 'S.N' },
    { key: 'name', label: 'Name' },
    { key: 'emp_id', label: 'Emp Id' },
    { key: 'total_working_days', label: 'Total Working Days' },
    { key: 'present_days', label: 'Present Days' },
    { key: 'absent_days', label: 'Absent Days' },
    { key: 'leaves_taken', label: 'Leaves Taken' },
    { key: 'action', label: 'Action' }
  ];

  const empAttendanceSummarySlots = {
    sn: (_, index) => (
      <span>{index + 1}</span>
    ),
    action: item => (
      <span onClick={() => activeTabType(tabType === 'team' ? "employee" : "empLogs", item)}>
        <img src="images/nextaction-Icon.svg" alt="" width={27} />
      </span>
    )
  };

  /* --------------Code End Team Section---------------------------- */

  /* --------------Code Start All Team Section---------------------------- */

  const deptAttendanceSummeryFields = [
    { key: 'sn', label: 'S.N' },
    { key: 'deptName', label: 'Dept Name' },
    { key: 'totalEmp', label: 'Total Emp' },
    { key: 'deptManager', label: 'Dept Manager' },
    { key: 'netWorking', label: 'Net Working' },
    { key: 'grossWorking', label: 'Gross Working' },
    { key: 'breakHrs', label: 'Break Hrs' },
    { key: 'action', label: 'Action' }
  ];

  const deptAttendanceSummerySlots = useMemo(() => ({
    sn: (_, index) => (
      <span>{index + 1}</span>
    ),
    action: item => (
      <span onClick={() => activeTabType("deptWiseEmp", item)}>
        <img src="images/nextaction-Icon.svg" alt="" width={27} />
      </span>
    )
  }), [deptAttendanceSummeryData, activeTabType]);

  /* --------------Code End All Team Section---------------------------- */
  return (
    <>
      <div className="ticketScoreSection">
        <div className="cardBox myRequest mb-3">
          <div className="cardHead d-flex justify-content-between">
            <h3>
              <img
                src="images/other-attendance.svg"
                alt=""
                width={40}
                className="me-2"
              />{" "}
              {t("headings.otherAttendance")}
            </h3>
      
              <CustomDateRangePicker
                onDateRangeChange={(dateRange) => handleFilterDateWiseData(dateRange)}
                endDefaultDate={endDate}
                startDefaultDate={startDate}

              />
              
         
          </div>
          <CustomTabs ulClass={"leaveTabs otherAttendance mb-4"} tabs={otherAttendanceTabs} />
          <div className="tab-content">
            <div className="tab-pane fade active show" id="self">
              <div className="d-flex justify-content-between align-items-center mb-4">
                <span className="underlineHead">Self Attendance Log</span>
                <input type="text" className="filterinpt" placeholder="Filter.." />
              </div>
              <CustomTable
                data={selfAttendanceData?.data}
                fields={selfLogFields}
                isLoading={selfAttendancePending}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                totalPages={selfAttendanceData?.totalPages}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={selfLogsScopedSlots}
              />
            </div>
            <div className="tab-pane fade" id="team">
              <div className="d-flex justify-content-between align-items-center mb-4">
                <BreadCrumb tabType={tabType} empName={empName} activeTabType={activeTabType} activeTab={activeTab} />
                <input type="text" className="filterinpt" placeholder="Filter.." />
              </div>
              {tabType === "employee" &&
                <CustomTable
                  data={selfAttendanceData?.data}
                  fields={selfLogFields}
                  isLoading={isLoading}
                  pagination={true}
                  currentPage={currentPage}
                  setCurrentPage={setCurrentPage}
                  totalPages={selfAttendanceData?.totalPages}
                  isHeader={false}
                  tableStyle={{ padding: "0px", boxShadow: "none" }}
                  scopedSlots={selfLogsScopedSlots}
                />}
              {tabType === "team" &&
                <CustomTable
                  data={myTeamAttendanceData?.data}
                  fields={empAttendanceSummaryFields}
                  isLoading={myTeamAttendancePending}
                  pagination={true}
                  currentPage={currentPage}
                  setCurrentPage={setCurrentPage}
                  totalPages={myTeamAttendanceData?.totalPages}
                  isHeader={false}
                  tableStyle={{ padding: "0px", boxShadow: "none" }}
                  scopedSlots={empAttendanceSummarySlots}
                />
              }
            </div>
            <div className="tab-pane fade" id="allTeam">
              <div className="d-flex justify-content-between align-items-center mb-4">
                <BreadCrumb
                  tabType={tabType}
                  empName={empName}
                  activeTabType={activeTabType}
                  activeTab={activeTab}
                  departmentName={departmentName}
                />
                <input type="text" className="filterinpt" placeholder="Filter.." />
              </div>

              {tabType === "allTeam" && <CustomTable
                data={deptAttendanceSummeryData}
                fields={deptAttendanceSummeryFields}
                isLoading={isLoading}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                totalPages={2}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={deptAttendanceSummerySlots}
              />}

              {tabType === "deptWiseEmp" && <CustomTable
                data={employeeAttendanceSummary}
                fields={empAttendanceSummaryFields}
                isLoading={isLoading}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                totalPages={2}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={empAttendanceSummarySlots}
              />}
              {tabType === "empLogs" &&
                <CustomTable
                  data={selfLogsData}
                  fields={selfLogFields}
                  isLoading={isLoading}
                  pagination={true}
                  currentPage={currentPage}
                  setCurrentPage={setCurrentPage}
                  totalPages={2}
                  isHeader={false}
                  tableStyle={{ padding: "0px", boxShadow: "none" }}
                  scopedSlots={selfLogsScopedSlots}
                />}

            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default OtherAttendanceTable