import React, { useEffect, useState } from 'react'
import OtherAttendanceTable from './OtherAttendanceTable'
import { types } from 'sass';
import moment from 'moment';
import AttendanceLogsModal from '../modal/AttendanceLogsModal';
import { useDispatch, useSelector } from 'react-redux';
import { pagination } from '../../utils/Constants.config';
import { employeeActions, otherAttendanceActions } from '../../core/resource/actions';
import { setDateFormate } from '../../utils/common';
import { CustomButton } from '../globalComponents';
import AttendanceReportModal from './AttendanceReportModal';


const attendanceLogs = [
  { time: "09:30:20", activity_name: "Main Gate IN", in_out: "IN" },
  { time: "09:33:34", activity_name: "Cafe IN", in_out: "OUT" },
  { time: "09:35:00", activity_name: "Cafe OUT", in_out: "IN" },
  { time: "09:30:20", activity_name: "Main Gate OUT", in_out: "OUT" },
  { time: "09:33:34", activity_name: "Main Gate IN", in_out: "IN" },
  { time: "09:35:00", activity_name: "Cafe IN", in_out: "OUT" },
  { time: "09:33:34", activity_name: "Cafe OUT", in_out: "IN" }
];
const OtherAttendance = () => {
  const dispatch = useDispatch()
  const {
    selfAttendancePending,
    selfAttendanceRes,
    selfAttendanceError,

    myTeamAttendancePending,
    myTeamAttendanceRes,
    myTeamAttendanceError,

    allTeamAttendancePending,
    allTeamAttendanceRes,
    allTeamAttendanceError,

    attendanceReportPending,
    attendanceReportRes,
    attendanceReportError,

  } = useSelector(state => state.otherAttendance)
  const {

    getAllEmployeeRes,
    getAllEmployeeError,
    getAllEmployeePending

  } = useSelector(state => state.employee)



  const [selectedTeamMemberData, setSelectedTeamMemberData] = useState([])
  const [empName, setEmpName] = useState('')
  const [tabType, setTabType] = useState('')
  const [activeTab, setActiveTab] = useState('')
  const [departmentName, setDepartmentName] = useState('')
  const [startDate, setStartDate] = useState(moment().subtract(10, 'days').format('YYYY-MM-DD'))
  const [endDate, setEndDate] = useState(moment().format('YYYY-MM-DD'))
  const [isShowAttendanceLogs, setIsShowAttendanceLogs] = useState(false)
  const [currentPage, setCurrentPage] = useState(1)
  const [selfAttendanceData, setSelfAttendanceData] = useState('')
  const [selectedEmployeeData, setSelectedEmployeeData] = useState({})
  const [myTeamAttendanceData, setMyTeamAttendanceData] = useState({})
  const [selectedEmpData, setSelectedEmpData] = useState('')
  const [allTeamAttendance, setAllTeamAttendance] = useState('')
  const [isShowAttendanceModal, setIsShowAttendanceModal] = useState(false)
  const [allEmployeeData, setAllEmployeeData] = useState('')
  const otherAttendanceTabs = [
    { name: "Self", onClickFun: (id) => getOtherAttendance(id), id: "self" },
    { name: "My Team ", onClickFun: (id) => getOtherAttendance(id), id: "team" },
    { name: "All Team", onClickFun: (id) => getOtherAttendance(id), id: "allTeam" },
  ];

  const getOtherAttendance = (type, selectedEmp, startDates, endDates) => {
    let params = {
      start_date: startDates || startDate,
      end_date: endDates || endDate,
      page: currentPage,
      limit: pagination.limit,
    }
    if (selectedEmp) {
      params = { ...params, emp_id: selectedEmp?._id || '' }
    }
    if (type === "team") {
      setTabType(type)
      dispatch(otherAttendanceActions.myTeamAttendanceRequest(params));

    } else if (type === "allTeam") {
      setTabType(type)
      dispatch(otherAttendanceActions.allTeamAttendanceRequest(params))

    } else {
      // setTabType(type)
      dispatch(otherAttendanceActions.selfAttendanceRequest(params));
    }
    setActiveTab(type)
    if (!selectedEmp) {
      setCurrentPage(1)
      setSelectedEmpData('')
    }


  }


  const activeTabType = (tabType, data) => {
    setTabType(tabType)
    // setActiveTab(tabName)
    if (data) {
      setEmpName(data?.name)
      data?.deptName && setDepartmentName(data?.deptName)
    }
    setSelectedEmpData(data)
    getOtherAttendance(tabType, data)

  }


  const handleFilterDateWiseData = (dateRange) => {
    const startDate = dateRange[0] && setDateFormate(dateRange[0], 'YYYY-MM-DD')
    const endDate = dateRange[1] && setDateFormate(dateRange[1], 'YYYY-MM-DD')
    if (startDate && endDate) {
      setStartDate(startDate)
      setEndDate(endDate)
      getOtherAttendance(tabType, "", startDate, endDate)
    }

  }

  /* Toggle Attendance Logs */
  const toggleAttendanceLogs = (empData) => {
    if (empData) {
      setIsShowAttendanceLogs(!isShowAttendanceLogs)
      setSelectedEmployeeData(empData)
    }
    setIsShowAttendanceLogs(!isShowAttendanceLogs)
  }




  /*Get My Team Attendance */
  useEffect(() => {
    if (!myTeamAttendancePending) {
      if (myTeamAttendanceError) {
        return;
      }
      if (myTeamAttendanceRes) {
        if (myTeamAttendanceRes?.code === 200) {
          setMyTeamAttendanceData(myTeamAttendanceRes?.data)
        }
      } 
    }
  }, [
    myTeamAttendanceRes,
    myTeamAttendanceError,
    myTeamAttendancePending
  ]);



  useEffect(() => {
    getOtherAttendance(activeTab, selectedEmpData)
  }, [currentPage])


  /* Get Self Attendance Data */
  useEffect(() => {
    if (!selfAttendancePending) {
      if (selfAttendanceError) {
        return;
      }
      if (selfAttendanceRes) {
        if (selfAttendanceRes?.code === 200) {
          setSelfAttendanceData(selfAttendanceRes?.data)
        }
      }
    }
  }, [
    selfAttendanceRes,
    selfAttendanceError,
    selfAttendancePending
  ]);



  /* Get All Team Attendance Data */
  useEffect(() => {
    if (!allTeamAttendancePending) {
      if (allTeamAttendanceError) {
        return;
      }
      if (allTeamAttendanceRes) {
        if (allTeamAttendanceRes?.code === 200) {
          setAllTeamAttendance(allTeamAttendanceRes?.data)
        }
      }
    }
  }, [
    allTeamAttendanceRes,
    allTeamAttendanceError,
    allTeamAttendancePending
  ]);

  const toggleAttendanceReportModal = () => {
    setIsShowAttendanceModal(!isShowAttendanceModal)
  }


  /* Get All Employee List*/
  useEffect(() => {
    getAllEmployees()
  }, [])
  const getAllEmployees = () => {
    dispatch(employeeActions.getAllEmployeeRequest())
  }
  useEffect(() => {
    if (!getAllEmployeePending) {
      if (getAllEmployeeError) {
        return;
      }
      if (getAllEmployeeRes) {
        if (getAllEmployeeRes?.code === 200) {

          setAllEmployeeData(getAllEmployeeRes?.data)
        }
      }
    }
  }, [
    getAllEmployeeRes,
    getAllEmployeeError,
    getAllEmployeePending
  ]);

  const downloadAttendanceReport = (filters) => {
    dispatch(otherAttendanceActions.attendanceReportRequest(filters))
  }

  useEffect(() => {
    if (!attendanceReportPending) {
      if (attendanceReportError) {
        dispatch(otherAttendanceActions.handleClearOtherAttendanceStore("HANDLE_CLEAR_ATTENDANCE_REPORT_STORE"))
        return;
      }
      if (attendanceReportRes) {
        if (attendanceReportRes?.code === 200) {
          const fileUrl = attendanceReportRes?.data?.filePath;
          // Download the file programmatically
          const link = document.createElement('a');
          link.href = fileUrl;
          link.download = fileUrl.split('/').pop(); // Extract the file name from the URL
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
          dispatch(otherAttendanceActions.handleClearOtherAttendanceStore("HANDLE_CLEAR_ATTENDANCE_REPORT_STORE"));
          toggleAttendanceReportModal();
        }
      }
    }
  }, [
    attendanceReportRes,
    attendanceReportError,
    attendanceReportPending
  ]);


  return (
    <>
      <div className="ticketScoreSection">
        <div className="row leavesMain mb-3">
          <div className="col-md-12 pe-md-2">
            <div className="cardBox leaveAppli ratingReview referral h-100 ">
              <div className='d-flex gap-3 justify-content-end'>
                <CustomButton
                  text={"Absconding Report"}
                // classNames="btn btn-?"
                // handleClick={() => {
                //     handleUpdateHierarchy(item);
                // }}
                />
                <CustomButton
                  text={"Attendance Report"}
                  classNames="btn btn-primary"
                  handleClick={() => {
                    toggleAttendanceReportModal();
                  }}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <OtherAttendanceTable
        otherAttendanceTabs={otherAttendanceTabs}
        selectedTeamMemberData={selectedTeamMemberData}
        setSelectedTeamMemberData={setSelectedTeamMemberData}
        empName={empName}
        activeTabType={activeTabType}
        tabType={tabType}
        activeTab={activeTab}
        departmentName={departmentName}
        endDate={endDate}
        startDate={startDate}
        handleFilterDateWiseData={handleFilterDateWiseData}
        toggleAttendanceLogs={toggleAttendanceLogs}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        selfAttendanceData={selfAttendanceData}
        myTeamAttendanceData={myTeamAttendanceData}
        allTeamAttendance={allTeamAttendance}

      />

      {isShowAttendanceLogs && <AttendanceLogsModal
        isOpen={isShowAttendanceLogs}
        handleOpenClose={toggleAttendanceLogs}
        selectedData={selectedEmployeeData}
      />
      }
      {isShowAttendanceModal && <AttendanceReportModal
        handleOpenClose={toggleAttendanceReportModal}
        isOpen={isShowAttendanceModal}
        allEmployeeData={allEmployeeData}
        downloadAttendanceReport={downloadAttendanceReport}
      />

      }
    </>
  )
}

export default OtherAttendance