import React, { useState } from 'react'
import { useCustomTranslation } from '../../translation'
import { CustomButton, CustomDateRangePicker } from '../globalComponents'
import { Input, Label } from '../commanComponent'
import CustomSelect from '../commanComponent/CustomSelect'
import moment from 'moment'
import { multiSelectIDs, setDateFormate } from '../../utils/common'
import { attendanceReportType } from '../../utils/Constants.config'
import { useSelector } from 'react-redux'
const AttendanceReportModal = (props) => {
    const { isOpen, handleOpenClose, downloadAttendanceReport, allEmployeeData } = props
    const { attendanceReportPending } = useSelector(state => state.otherAttendance)
    const [startDate, setStartDate] = useState('')
    const [endDate, setEndDate] = useState('')
    const [reportForm, setReportForm] = useState({
        reportType: null,
        dateRange: "",
        employees_ids: null
    })
    const { t } = useCustomTranslation()

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e?.target : { value: e };

        setReportForm(prevState => {
            let updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });
    };

    const handleFilterDateWiseData = (dateRange) => {
        const startDate = dateRange[0] && setDateFormate(dateRange[0], 'YYYY-MM-DD')
        const endDate = dateRange[1] && setDateFormate(dateRange[1], 'YYYY-MM-DD')
        if (startDate && endDate) {
            setStartDate(startDate)
            setEndDate(endDate)
        }

    }
    const handleDownloadAttendanceReport = () => {
        let filterReport = {}
        if (reportForm?.reportType?.value) filterReport["report_type"] = reportForm?.reportType?.value
        if (startDate && endDate) {
            filterReport["start_date"] = startDate
            filterReport["end_date"] = endDate
        }
        if (reportForm?.employees) filterReport["employees_ids"] = multiSelectIDs(reportForm?.employees)

        downloadAttendanceReport(filterReport)
    }

    return (
        <>
            <div class={`modal fade correctionAprovel ${isOpen ? ' show' : ''}`} id="correctionAp"
                tabIndex="-1"
                style={{ display: isOpen ? 'block' : 'none', }}
                aria-hidden={!isOpen}
            >
                <div class="modal-dialog modal-dialog-centered" style={{ maxWidth: "550px" }}>
                    <div class="modal-content rounded-0 border-0">
                        <div class="modal-header ">
                            <h1 class="modal-title fs-5 fw-bold text-primary">Download Reports</h1>
                            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"
                                onClick={() => handleOpenClose()}
                            ></button>
                        </div>
                        <div class="modal-body pt-0">
                            <div className="formPanel mt-4">

                                <div className="row">
                                    <div className="col-md-3">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.reportType")} required={true} />
                                        </div>
                                    </div>
                                    <div className="col-md-9">
                                        <div className="form-group mb-3">
                                            <CustomSelect
                                                data={attendanceReportType || []}
                                                placeholder={t("placeholders.reportType")}
                                                value={reportForm?.reportType || ''}
                                                handleChange={(selectedOption) => handleFromChange(selectedOption, "reportType")}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-3">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.date")} required={true} />
                                        </div>
                                    </div>
                                    <div className="col-md-9">
                                        <div className="form-group mb-3">
                                            <CustomDateRangePicker
                                                onDateRangeChange={(dateRange) => handleFilterDateWiseData(dateRange)}
                                                endDefaultDate={endDate}
                                                startDefaultDate={startDate}
                                                sizeType={"full"}

                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-3">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.employees")} required={true} />
                                        </div>
                                    </div>
                                    <div className="col-md-9">
                                        <div className="form-group mb-3">
                                            <CustomSelect
                                                data={allEmployeeData || []}
                                                placeholder={t("placeholders.employees")}
                                                value={reportForm?.employees || ''}
                                                handleChange={(selectedOption) => handleFromChange(selectedOption, "employees")}
                                                isMulti={true}
                                            />
                                        </div>
                                    </div>
                                </div>

                            </div>


                        </div>
                        <div className='modal-footer'>
                            <div class="d-flex justify-content-end gap-3">
                                <CustomButton
                                    text={t("btns.download")}
                                    classNames={"btn-success "}
                                    handleClick={() => handleDownloadAttendanceReport()}
                                    isLoading={attendanceReportPending}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {isOpen && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default AttendanceReportModal
