import React from 'react'

const BreadCrumb = (props) => {
    const { tabType, empName, activeTabType,departmentName } = props
    const renderedBreadCrumb = () => {
        if (tabType === "employee") {
            return (
                <ul className="breadCrumb mb-3">
                    <li>
                        <span onClick={() => activeTabType("team")}>My Team</span> &gt;{" "}
                    </li>
                    <li className="active">{empName}</li>
                </ul>
            );
        } else if (tabType === "team") {
            return <span className="underlineHead">My Team</span>;
        }
        else if (tabType === "deptWiseEmp") {
            return (
                <ul className="breadCrumb mb-3">
                    <li>
                        <span onClick={() => activeTabType("allTeam")}>All Teams</span> &gt;{" "}
                    </li>
                    <li className="active" onClick={() => activeTabType("deptWiseEmp")}>{departmentName}</li>
                </ul>
            )
        }

        else if (tabType === "empLogs") {
            return (
                <ul className="breadCrumb mb-3">
                    <li>
                        <span onClick={() => activeTabType("allTeam")}>All Teams</span> &gt;{" "}
                    </li>
                    <li>
                        <span onClick={() => activeTabType("deptWiseEmp")}>{departmentName}</span> &gt;{" "}
                    </li>
                    <li className="active">{empName}</li>
                </ul>
            );
        } else if (tabType === "allTeam") {
            return (
                <span className="underlineHead" >
                    All Teams
                </span>
            );
        }

        return null;
    }
    return (
        <>
            {renderedBreadCrumb()}
        </>
    )
}

export default BreadCrumb
